*
{
    margin:0;
    padding:0;  
    
}
.home
{
    
   /* max-width:1000px;
    margin:0 auto;
    margin-top:90px;*/
    height:300%;
    background-image: url("home.jpg");
    background-size: cover;
    background-size: 100%;
    background-repeat: no-repeat;

}
.home__container
{
    height:100vh;
    display:flex;
    flex-direction:column;
    align-items:end;
    margin-top: 80px;
}
.home__formContainer {
    box-shadow:1px 4px 12px -2px rgba(0,0,0,4.3);
    background:#0a288a75;
    padding:10px;
    border-radius:10px;
    width:300px;
    position: absolute;
    top: 95px;
    right: 30px;
}
.home__formContainer input{
    border:1px solid  rgb(31, 16, 238);
    outline:none;
    padding:5px 5px;
    margin: 5px 10px;
    padding-left: 10px;
    width:80%;
    border-radius:8px;
    background:transparent;
    color:white;
    font-size:14px;
}
.home__formContainer input::placeholder
{
    color:white;
}
.home__formContainer button{
    /*background: transparent;*/
border:1px solid rgb(19, 9, 161);
outline:none;
font-size:18px;
color:rgb(143, 17, 201);
padding:7px;
width:200px;
margin-top:30px;
border-radius:12px;
cursor:pointer;
margin-left: 50px;
}
.home__title{
    text-align: center;
}