
.user{

    box-shadow: 10px 8px 15px -2px rgba(135,135,135,0.37);
    background-color: rgb(133, 121, 121);
    margin-bottom:30px;
    width:600px;
    padding:20px;
    align-items: center;
    justify-content: center;
    margin-left: 350px;
    border-radius: 10px;
    }
    .user h2,.user h3{
        color:rgba(0,0,0,0.541);
    }
    .user span
    {
    font-weight:500;
    color:black;
    }
    .user__bottom
    {
    margin-top:20px;
    }
    .user__delete
    {
    border:none;
    outline:none;
    padding:10px;
    width:100px;
    background-color:rbga(255,0,0,0.818);
    border-radius:8px;
    color:black;
    cursor:pointer;
    }